import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import flagBg from '../../assets/images/bg-flag.png';
import flagUs from '../../assets/images/gb.svg';
import { faImage, faHome, faGifts, faMobileAlt, faBeer, faLink, faBlog } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './Header.scss'; 

class Header extends Component {
    constructor() {
        super();

        this.hideMenu = this.hideMenu.bind(this);
    }

    hideMenu(e) {
        if(window.innerWidth < 877) {
            const menu = document.getElementById('header-menu-nav');
            const menuBtn = document.getElementById('menu-btn');
            const nav = document.getElementById('nav-links');

            this.props.onMenuClicked();
            menuBtn.className = '';
            menu.style.display = 'none';

            if(this.props.isBeerClicked) {
                this.props.onBeerClicked();
                nav.style.display = 'none';
            }
        }
    }

    render() {
        return (
            <header className="header-menu" >
                <aside className="aside-left">
                </aside>
                <div id="header-menu-nav" className="menu-middle">
                    <nav className="animated pulse">
                        <div onClick={this.hideMenu}>
                            <Link to="/home">
                                <span><FontAwesomeIcon className="ico" icon={faHome}  title={this.props.isBg ? "" : "Home"} /></span>
                                <span className="link-name">{this.props.isBg ? "" : "HOME"}</span>
                            </Link>
                        </div>
                        <div onClick={this.hideMenu}>
                            <Link to="/beers">
                                <span><FontAwesomeIcon className="ico" icon={faBeer}  title={this.props.isBg ? "" : "Beers"} /></span>
                                <span className="link-name">{this.props.isBg ? "" : "BEERS"}</span>
                            </Link>
                        </div>
                        <div onClick={this.hideMenu}>
                            <Link to="/offers">
                                <span><FontAwesomeIcon className="ico" icon={faGifts}  title={this.props.isBg ? "" : "Offers"}/></span>
                                <span className="link-name">{this.props.isBg ? "" : "OFFERS"}</span>
                            </Link>
                        </div>
                        <div onClick={this.hideMenu}>
                            <Link to="/gallery">
                                <span><FontAwesomeIcon className="ico" icon={faImage}  title={this.props.isBg ? "" : "Galery"}/></span>
                                <span className="link-name">{this.props.isBg ? "" : "GALLERY"}</span>
                            </Link>
                        </div>
                        <div onClick={this.hideMenu}>
                            <Link to="/blog">
                                <span><FontAwesomeIcon className="ico" icon={faBlog}  title={this.props.isBg ? "" : "Blog"}/></span>
                                <span className="link-name">{this.props.isBg ? "" : "BLOG"}</span>
                            </Link>
                        </div>
                        <div onClick={this.hideMenu}>
                            <Link to="/friends">
                                <span><FontAwesomeIcon className="ico" icon={faLink}  title={this.props.isBg ? "" : "Friends"}/></span>
                                <span className="link-name">{this.props.isBg ? "" : "FRIENDS"}</span>
                            </Link>
                        </div>
                        <div onClick={this.hideMenu}>
                            <Link to="/contacts">
                                <span><FontAwesomeIcon className="ico" icon={faMobileAlt}  title={this.props.isBg ? "" : "Contacts"}/></span>
                                <span className="link-name">{this.props.isBg ? "" : "CONTACTS"}</span>
                            </Link>
                        </div>
                        <div className="float-right" onClick={this.hideMenu}>
                                <span>
                                    <img src={!this.props.isBg ? flagBg : flagUs} alt="BG" width="30px" height="25px" onClick={this.props.onChangeLang} title={this.props.isBg ? "" : "Language"}/>
                                </span>
                            {/* <Link to="/">
                                <span>
                                    <img src={!this.props.isBg ? flagBg : flagUs} alt="BG" width="30px" height="25px" onClick={this.props.onChangeLang} title={this.props.isBg ? "" : "Language"}/>
                                </span>
                            </Link> */}
                        </div>
                    </nav>
                </div>
                <aside className="aside-right">
                </aside>
            </header>
        );
    }
}

const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg,
    isMenuClicked: state.buttons.isMenuClicked,
    isBeerClicked: state.buttons.isBeerClicked
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang()),
    onMenuClicked: () => dispatch(actionCreator.menuClicked()),
    onBeerClicked: () => dispatch(actionCreator.beerClicked())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Header);
