import React, { Component } from 'react';
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import { beers } from '../../assets/beers/all-beers';
import PaginatorArticle from '../PaginatorArticle/PaginatorArticle';
import './Beers.scss';

class Beers extends Component {
  constructor() {
    super();

    this.filters = ['alcohol free', 'ale', 'dark', 'lager', 'organic', 'red', 'unpasteurized', 'weiss'];

    this.state = {
      beerList: []
    }

    this.filtr = this.filtr.bind(this);
  }

  componentDidMount() {
    this.setState({ beerList: beers })
  }

  filtr(e) {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    
    let filterType = e.target.id;

    this.setState({beerList: beers.filter(obj => {
      return obj.type.includes(filterType);
    })})
  }

  render() {
      const beers = this.state.beerList.map((beer, j) => {
        return <article key={j} className="beer-article">
          <div id="article-buttons-wrapper">
          {beer.type.split(', ').map((name, i) => { return <button key={i+100} id={name} className="filter-btn article" onClick={this.filtr}>{name}</button>})}
          </div>
          <hr />
          {PaginatorArticle(beer)}
          <br />
          <br />  
          <br />
        </article>
      });

      return (
          <div className="beers-filter">
              <br />
              <br />
              <br />
              <h1>{this.props.isBg ? "" : "Beers"}</h1>
              {/* <br /> */}
              {this.filters.map((name, i) => { return <button key={i} id={name} className="filter-btn" onClick={this.filtr}>{name}</button>})}
              <br />
              <br />
              <br />
              <br />
              <div style={window.innerWidth < 600 ? {width: "80%", display: "block", margin: "0 auto"} : {width: "50%", display: "block", margin: "0 auto"} }>
                {beers}
              </div>
              <br />
              <br />
              {this.filters.map((name, i) => { return <button key={i} id={name} className="filter-btn" onClick={this.filtr}>{name}</button>})}
              <br />
              <br />
          </div>
      )
    }
}


const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Beers);
