import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PaginatorArticle from '../PaginatorArticle/PaginatorArticle';
import { connect } from "react-redux";
import * as actionCreator from '../../redux/actions';
// import { beers as beerEn } from '../../assets/beers/all-en';
// import { beers as beerBg } from '../../assets/beers/all-bg';
import { beers } from '../../assets/beers/all-beers'
// import { beers as beerEn } from '../../assets/beers/beers-en';
// import { beers as beerBg } from '../../assets/beers/beers-bg';
import './Paginator.scss';

class Paginator extends Component {
  constructor(props) {
    super(props);

    // this.objectsArray = this.props.isBg ? beerBg.filter(beer => { return beer.brand ===  this.props.name}) : beerEn.filter(beer => { return beer.brand ===  this.props.name})
    this.objectsArray = beers.filter(beer => { return beer.brand ===  this.props.name}) 

    if(this.objectsArray.length % 2 > 0) {
      this.objectsArray.push({ "name": "empty", "size": 0 });
    }

    this.divider = Math.floor(this.objectsArray.length / this.props.objectsPerPage);
    
    this.state = {
      page: 0,
      pagesNumber: this.objectsArray.length % this.props.objectsPerPage !== 0 ? this.divider + 1 : this.divider,
      pageColor: '#E8A813'
    }

    this.clickedPage = this.clickedPage.bind(this);
  }

  clickedPage(e) {
    e.preventDefault();
    document.getElementsByClassName('active')[0].style.backgroundColor = 'transparent';  
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;

    if(e.target.id === 'first') {
        document.getElementById(this.state.page+1).className = '';
        this.setState({page: 0});
        document.getElementById('1').className = 'active';

    } else if(e.target.id === 'last') {
        document.getElementById(this.state.page+1).className = '';
        this.setState({page: this.state.pagesNumber - 1});
        document.getElementById(this.state.pagesNumber).className = 'active';

    } else {
        document.getElementById(this.state.page+1).className = '';
        this.setState({page: +e.target.id - 1});
        document.getElementById(+e.target.id).className = 'active';
    }
    document.getElementsByClassName('active')[0].style.backgroundColor = this.state.pageColor;
  }

  render() {
    const currentPageArr = this.objectsArray.slice(this.state.page*this.props.objectsPerPage, this.state.page*this.props.objectsPerPage+this.props.objectsPerPage);
    const page = currentPageArr.map((item, i) => { return <li key={i}>{PaginatorArticle(item)}</li> });

    return (
        <div>
          <div id="pagination" className="pagination">
              <a href="/" id="first" onClick={this.clickedPage}>&laquo;</a>
              <a href="/" id="1" className="active" onClick={this.clickedPage} style={{backgroundColor: this.state.pageColor}}>1</a>
              {[...Array(this.state.pagesNumber-1)].map((page, i) => {return <a key={i} id={i+2} href="/" onClick={this.clickedPage}>{i+2}</a>})}
              <a href="/" id="last" onClick={this.clickedPage}>&raquo;</a>
          </div>
          <br />
          <br />
          <ul id="paginator-wrapper">
              {page}
          </ul>
        </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Paginator);

Paginator.propTypes = {
  name: PropTypes.string,
  objectsPerPage: PropTypes.number
}
