import React from 'react';

import { faStar } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IS_BG } from '../../services';
import { LoadingImage as Loading } from '../Loading/LoadingImage';
import './PaginatorArticle.scss';

const PaginatorArticle = ({ name, alc, price, image, size, description, info, link}) => {
    const stars = num => {
        const starList = [];
        
        for(let i = 0; i < num; i += 1) {
            starList.push(<span key={i}><FontAwesomeIcon style={{ color: "#F6B618" }} className="ico" icon={faStar} /></span>)
        }

        for(let i = 0; i < 5 - num; i += 1) {
            starList.push(<span key={i+10}><FontAwesomeIcon style={{ color: "#D5D3D2" }} className="ico" icon={faStar} /></span>)
        }

        return starList;
    }

    // let prize = (parseFloat(price) - 0.15).toFixed(2);

    return size !== 0 ? <div key={name+size}>
        <article className="paginator-article" style={{fontWeight: "bolder", color: "#47332D"}}>
            <div className="image-wrapper">
                <div className="container-wrapper">
                    <div className="left">
                        <h2 className="article-title">
                            {name.replace('-', ' ')}
                        </h2>
                        <h4 className="article-alc">{!IS_BG() ? "Alc: " + alc + "%" : ": "  + alc + "%"}</h4>
                        <h3 className="article-price">{!IS_BG() ? "Price: "  + price + " lv" : ": "  + price + " "}</h3>
                        {/* <h5>{!IS_BG() ? "*  price without the bottle" : "*   "}</h5> */}
                    </div>
                    <div className="right">
                        {image ? Loading(image, name, size) : null}
                    </div>
                </div>
            </div>
            <div className="article-description">
                <br />
                <strong className="article-bitterness">{!IS_BG() ? "Bitterness: " : ": " }</strong>
                {info ? stars(info["bitterness"]) : null}
                <br />
                <br />
                <strong className="article-smell">{!IS_BG() ? "Smell: " : ": " }</strong>
                {info ? stars(info["smell"]) : null}
                <br />
                <br />
                <strong className="article-impression">{!IS_BG() ? "Impression: " : ": " }</strong>
                {info ? stars(info["impression"]) : null}
                <br />
                <br />
                <br />
                <div className="article-description-words">
                    {!IS_BG() ? description.en : description.bg}
                </div>
                <br />
                <br />
                <hr />
                {!IS_BG() ? "Producer's website: " : "  : " }
                {link ? <h3 style={{textAlign: "center"}}><a href={link} target="_blank" rel="noopener noreferrer">{link.replace('https://', '').replace('http://', '').replace('www.', '').split('/')[0]}</a></h3> : !IS_BG() ? " not found" : "   "}
                <br />
            </div>
        </article>
        {/* <br />
        <div style={{fontWeight: "bolder", color: "#47332D"}}>{description}</div> */}
    </div> : <article className="paginator-article" style={{position: "hidden"}}></article>
}

export default PaginatorArticle;