import React, { Component } from 'react';
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";

import logo from '../../assets/images/logo.png';
// import facebook from '../../assets/images/facebook.png';
import { faBeer} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './Logo.scss';


class Logo extends Component {
    constructor(props) {
        super(props);

        this.toggleBeersBtn = this.toggleBeersBtn.bind(this);
        this.toggleMenuBtn = this.toggleMenuBtn.bind(this);
    }
    
    toggleMenuBtn() {
        const menuBtn = document.getElementById('menu-btn');
        const menu = document.getElementById('header-menu-nav');
        const pagination = document.getElementById('pagination');

        if (this.props.isBeerClicked) {
            this.toggleBeersBtn();
        }

        if (!this.props.isMenuClicked) {
            menuBtn.className += ' open';
            menu.style.display = 'inherit';
            pagination ? pagination.style.position = 'inherit' : console.log();

            this.props.onMenuClicked();
        } else {
            menuBtn.className = '';
            menu.style.display = 'none';

            if (this.props.isBeerClicked) {
                pagination ? pagination.style.position = 'inherit' : console.log();
            } else {
                pagination ? pagination.style.position = 'fixed' : console.log();
            }

            this.props.onMenuClicked();
        }
    }
    
    toggleBeersBtn() {
        let that = this;
        const navLinks = document.getElementById('nav-links');
        const menu = document.getElementById('header-menu-nav');
        const menuBtn = document.getElementById('menu-btn');
        const pagination = document.getElementById('pagination');

        if (this.props.isMenuClicked) {
            this.toggleMenuBtn();
        }

        if (!this.props.isBeerClicked) {
            navLinks.style.display = 'inherit';
            pagination ? pagination.style.position = 'inherit' : console.log();

            this.props.onBeerClicked();
        } else {
            navLinks.style.display = 'none';
            
            if (this.props.isMenuClicked) {
                pagination ? pagination.style.position = 'inherit' : console.log();
            } else {
                pagination ? pagination.style.position = 'fixed' : console.log();
            }
            this.props.onBeerClicked();
        }

        navLinks.addEventListener('click', function() {
            navLinks.style.display = 'none';
            menuBtn.className = '';
            menu.style.display = 'none';

            that.props.onBeerClicked();

            if (that.props.isMenuClicked) {
                that.props.onMenuClicked();
            }
        })
    }

    componentDidMount() {
        document.getElementById('logoImg').className.replace('animated', '');
    }

    render() {
        return (
            <header className="logo-header">
                <aside className="aside-left">
                    <div id="beers-btn" onClick={this.toggleBeersBtn}>
                        <span><FontAwesomeIcon className="ico" icon={faBeer} /></span>
                    </div>
                </aside>
                <div className="logo-wrapper">
                    <div>
                        {/* <a className="facebook" href="https://www.facebook.com/Birenoto.shop" target="_blank" rel="noopener noreferrer" title="facebook">
                            <img src={facebook} alt="fb" width="30px" />
                        </a> */}
                        <img id="logoImg" className="animated pulse" src={logo} alt="" width="100%" height="100%" />
                    </div>
                </div>
                <aside className="aside-right">
                    <div id="menu-btn" onClick={this.toggleMenuBtn}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                </aside>
            </header>
        )
    }
}

const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg,
    isMenuClicked: state.buttons.isMenuClicked,
    isBeerClicked: state.buttons.isBeerClicked
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang()),
    onMenuClicked: () => dispatch(actionCreator.menuClicked()),
    onBeerClicked: () => dispatch(actionCreator.beerClicked())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Logo);