import React, { Component } from 'react';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faIgloo } from '@fortawesome/free-solid-svg-icons';
import { Helmet } from "react-helmet";
import Header from './components/Header/Header';
import Logo from './components/Logo/Logo';
import Nav from './components/Nav/Nav';
import links from './assets/js/nav-links';
import logoImg from './assets/images/logo.png';
import Main from './components/Main/Main';
import Footer from './components/Footer/Footer';
import './App.scss';

library.add(faIgloo);

class App extends Component {
    componentDidMount() {
        localStorage.setItem('isBg', true);
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
    }

    render() {
        return (
          <div>
              <Helmet>
                  <title></title>
                  <meta charSet="utf-8" />
                  <meta name="description" content="Czech beers, birenoto, beers, bira" />
                  <link rel="canonical" href={"http://localhost:3000/home"} />
              </Helmet>
              <div id="main-wrapper" className="wrapper">
                  <header className="header">
                      <Logo />
                      <Header />
                      <Nav links={links}/>
                  </header>
                  <Main/>
                  <Footer bg={"#47332D"} logo={logoImg} year={2019}/>
              </div>
          </div>
        )
    }
}

export default App;