import { CHANGE_LANG, BEER_CLICKED, MENU_CLICKED } from '../actions/types';
import { INITIAL_STATE } from '../initialState';

const buttonsReducer = (state = INITIAL_STATE, action) => {
  const newState = { ...state };
  let currentVal;

  switch (action.type) {

    case CHANGE_LANG:
      currentVal = newState.isBg;

      newState.isBg = !currentVal;
      localStorage.setItem('isBg', newState.isBg);
      break;

    case MENU_CLICKED:
      currentVal = newState.isMenuClicked;

      newState.isMenuClicked = !currentVal;
      localStorage.setItem('isMenuClicked', newState.isMenuClicked);
      break;

    case BEER_CLICKED:
      currentVal = newState.isBeerClicked;

      newState.isBeerClicked = !currentVal;
      localStorage.setItem('isBeerClicked', newState.isBeerClicked);
      break;

    default: break;
  }
  return newState;
};

export default buttonsReducer;

