import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import { LoadingImage as Loading } from '../Loading/LoadingImage';
import { allBlogs } from '../../assets/blog'
import "./Blog.scss";

class Blog extends Component {

  render() {
    const blog = allBlogs.map((blog, i) => {
      return <Link to={'/blog/' + blog.id} key={i}><article className="blog-wrapper">
        {Loading(blog.image, 'birenoto', 100)}
        <h3 className="blog-title">{this.props.isBg ? blog.title.bg : blog.title.en}</h3>
      </article></Link>
    });

    return (
      <div className="blog">
        <br />
        <br />
        <br />
        <h1>{this.props.isBg ? "" : "Blog"}</h1>
        <br />
        <br />
        {blog}
        <br />
        <br />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};

export default connect(
  mapStateToProps,
  mapDispachToProps
)(Blog);
