import React, { Component } from 'react';
import { Helmet } from "react-helmet";
import Paginator from '../../components/Paginator/Paginator';

const RouteHelmet = el => {
    return class extends Component {
        render() {
            return (
                <div>
                    <Helmet>
                        <title>{el}</title>
                        <meta charSet="utf-8" />
                        <link rel="canonical" href={window.location.origin + el.toLocaleLowerCase().replace(' ', '-')} />
                        <meta name="description" content={"birenoto, " + el + ", beers"} />
                    </Helmet>
                    <Paginator name={el} objectsPerPage={2} />
                </div>
            )
        }
    }
}

export default RouteHelmet;