import Admin from "./components/Admin/Admin";
import Friends from "./components/Friends/Friends";
import Blog from "./components/Blog/Blog";
import Home from "./components/Home/Home";
import Beers from "./components/Beers/Beers";
import Gallery from "./components/Gallery/Gallery";
import Offers from "./components/Offers/Offers";
import Contacts from "./components/Contacts/Contacts";
import RouteHelmet from "./components/RouteHelmet/RouteHelmet";
import BlogArticle from "./components/Blog/BlogArticle";
import { BRANDS } from './services/Constants';
import { allBlogs } from './assets/blog';

const routes = [
  {
    path: "/",
    exact: true,
    component: Home
  },
  {
    path: "/home",
    component: Home
  },
  {
    path: "/beers",
    component: Beers
  },
  {
    path: "/gallery",
    component: Gallery
  },
  {
    path: "/offers",
    component: Offers
  },
  {
    path: "/contacts",
    component: Contacts
  },
  {
    path: "/friends",
    component: Friends
  },
  {
    path: "/blog",
    exact: true,
    component: Blog
  },
  {
    path: "/admin",
    component: Admin
  }
];

BRANDS.forEach(el => {
    routes.push({
          path: `/${el.toLocaleLowerCase().replace(' ', '-')}`,
          exact: true,
          component: RouteHelmet(el)
        });
});

allBlogs.forEach(blog => {
    routes.push({
          path: `/blog/${blog.id}`,
          component: BlogArticle(blog)
        });
});

export default routes;