import React, { lazy, Suspense } from 'react';
import { faSpinner } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './LoadingImage.scss';

const ImageLoaded = lazy(() => import('./Image'));

export const LoadingImage = (path, name, size) => {

    return <Suspense fallback={<div id="spinner">
                <FontAwesomeIcon className="ico fa-spin" icon={faSpinner} /></div>}>
                <ImageLoaded path={path} name={name} size={size} />
            </Suspense>
        }