import React, { Component } from 'react';
import Img3 from '../../assets/images/herold-rebajas.png';
import Img1 from '../../assets/images/svijany-rebajas.png';
// import Img2 from '../../assets/images/cernahora-rebajas1.png';
import Img4 from '../../assets/images/dudak-rebajas.png';
import Img5 from '../../assets/images/promo-svijany10.png';
import './Carousel.scss';

class Carousel extends Component {
    constructor() {
        super();

        this.slides = '';
        this.slideIndex = 0;
        this.time = 7000;
        this.timeout = '';

        this.showSlides = this.showSlides.bind(this);
    }

    componentDidMount() {
        this.slides = document.getElementsByClassName("mySlides");
        this.showSlides();
    }

    componentWillUnmount() {
        clearTimeout(this.timeout);
    }

    showSlides() {
      for (let i = 0; i < this.slides.length; i += 1) {
        this.slides[i].style.display = "none";
      }

      this.slideIndex++;

      if (this.slideIndex > this.slides.length) {
        this.slideIndex = 1
      }

      this.slides[this.slideIndex-1].style.display = "initial";

      this.timeout = setTimeout(this.showSlides, this.time);
    }

    render() {
        let width = 97 + "%";
        let height = window.innerWidth < 600 ? 0.8*window.innerHeight/3 : 1.8*window.innerHeight/3;

        return (
            <div className="carousel-home">
                <div className="slideshow-container">
                    <div className="mySlides fade">
                        <img src={Img5} style={{width, height }} alt="IMG" />
                    </div>

                    {/* <div className="mySlides fade">
                        <img src={Img5}style={{width, height }} alt="IMG" />
                    </div>

                    <div className="mySlides fade">
                        <img src={Img5}style={{width, height }} alt="IMG" />
                    </div> */}
                </div>
            </div>
        )
    }
}

export default Carousel;