import React, { Component } from "react";
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import { LoadingImage as Loading } from '../Loading/LoadingImage';
import { PALIVEC_MAP, PALIVEC_42_MAP } from "../../services/Constants";
import palivecImg from '../../assets/images/Palivec.png';
import palivec42Img from '../../assets/images/Palivec42.png';
import './Friends.scss';

class Friends extends Component {
  render() {
    return (
      <div className="friends">
        <br />
        <br />
        <br />
        <h1>{this.props.isBg ? " " : "Our friends"}</h1>
        <a href="https://www.facebook.com/pubpalivec" target="_blank" rel="noopener noreferrer">
          {Loading(palivecImg, 'Palivec', 50)}
        </a>
        <br />
        <iframe title="palivec-map" src={PALIVEC_MAP} width="98%" height="300" frameBorder="0" allowFullScreen></iframe>
        <br />
        <br />
        <br />
        <a href="https://www.facebook.com/Palivec42" target="_blank" rel="noopener noreferrer">
          {Loading(palivec42Img, 'Palivec 42', 50)}
        </a>
        <br />
        <iframe title="palivec-map" src={PALIVEC_42_MAP} width="98%" height="300" frameBorder="0" allowFullScreen></iframe>
        <br />
        <br />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Friends);
