import React, { Component, } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import './Nav.scss';

class Nav extends Component {
  render() {
    const getLinks = this.props.links.map(l => {
      return <Link to={l.path} className="app-link" key={l.name} title={l.name}>
        <li className=" animated swing link" color={l.color} >
            <img src={l.img} alt="logo" width="52px" height="50px"/>
            {/* <span>{l.name}</span> */}
        </li>
      </Link>
    })

    return (
      <nav id="nav-links">
        <ul className="link-group">
          {getLinks}
          <br />
        </ul>
      </nav>
    )
  }
}

export default Nav;

Nav.propTypes = {
  links: PropTypes.array
}
