import React, { Component } from 'react';
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import svijanyImg from '../../assets/images/Svijany.png';
import kegMachine from '../../assets/images/keg-beer-tap.jpg';
import bouquet from '../../assets/images/bouquet.jpg';
import { LoadingImage as Loading } from '../Loading/LoadingImage';
import './Offers.scss';

const maxWidth = 630;

class Offers extends Component {
    render() {
        return (
            <div className="offers">
                <br />
                <br />
                <br />
                <h1>{!this.props.isBg ? "Offers" : ""}</h1>
                <br />
                <div className="offers-list">
                  <ul>
                    <li>
                      <h2>{!this.props.isBg ? "Tap beer" : " "}<br /> Svijany</h2>
                      {Loading(svijanyImg, 'Svijany', 35)}
                      <h3>0.5 l - 2.10 {!this.props.isBg ? "lv" : ""}<br /> 1.0 l - 4.20 {!this.props.isBg ? "lv" : ""}</h3>
                      {window.innerWidth < maxWidth ? <hr /> : null}
                    </li>
                    <li>
                      <h2>{!this.props.isBg ? <>Tap beer machine + keg <br /> for a rent</> : <>    +  <br />  </>}</h2>
                      {Loading(kegMachine, 'Tap beer machine + keg', 52)}
                      {window.innerWidth < maxWidth ? <hr /> : null}
                    </li>
                    <li>
                      <h2>{!this.props.isBg ? "A bouquet of beers" : " "}</h2>
                      {Loading(bouquet, 'A bouquet of beers', 52)}
                    </li>
                  </ul>
                </div>
            </div>
        )
    }
}


const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Offers);
