import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import facebook from '../../assets/images/facebook.png';
import './Footer.scss';

class Footer extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isOpen: false
        };
    }


    render() {
        return (
            <footer className="footer" style={{ backgroundColor: this.props.bg }} >
                <div className="footer-logo">
                    <Link to="/">
                        <h1><img id="footer-logo" src={this.props.logo} alt="" width="80%"/></h1>
                    </Link>
                </div>
                <div className="footer-contacts">
                    <a id="facebook" href="https://www.facebook.com/Birenoto.shop" target="_blank" rel="noopener noreferrer"><img src={facebook} alt="fb" width="30px" /></a>
                
                    <span className="copy">&nbsp;&nbsp;&nbsp;birenoto.me&copy;{this.props.year}</span>
                </div>
            </footer>
        );
    }
}

export default Footer;

Footer.propTypes = {
    bg: PropTypes.string,
    logo: PropTypes.string,
    year: PropTypes.number
}

