import React, { Component } from 'react';
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import { LoadingImage as Loading } from '../Loading/LoadingImage';
import './BlogArticle.scss';

const BlogArticle = blog => {
    
    class ArticleClass extends Component {
      render() {
        const paragraphs = blog.paragraphs.length === 0 ? (this.props.isBg ? <span>     ...<br /><br /></span> : <span>Still not ready ...<br /><br /></span>) : blog.paragraphs.map((p, i) => {
          return <div key={i} style={{textAlign: "justify"}}>
            <h3>{p.subtitle ? (this.props.isBg ? p.subtitle.bg : p.subtitle.en) : ""}</h3>
            {
              window.innerWidth > 780 ? 
              <div>
                <span style={{float: "right", margin: "0 0 2% 2%"}}>{p.image ? Loading(p.image, "", 96) : ""}</span>
                {p.text ? p.text.map((t, i) => { return <p key={i}>{this.props.isBg ? t.bg : t.en}</p> }) : ""}
              </div> : 
              <div>
                {p.text ? p.text.map((t, i) => { return <p key={i}>{this.props.isBg ? t.bg : t.en}</p> }) : ""}
                <span style={{float: "right", margin: "0 0 2% 2%"}}>{p.image ? Loading(p.image, "", 96) : ""}</span>
              </div>
            }
          </div>
        });

        return (
          <article className="blog">
              <br />
              <br />
              <br />
              {window.innerWidth > 780 ? Loading(blog.image, "", 50) : Loading(blog.image, "", 96)}
              <h1>{this.props.isBg ? blog.title.bg : blog.title.en}</h1>
              <h3>{blog.author ? (this.props.isBg ? " " + blog.author.bg : "by " + blog.author.en) : ""}</h3>
              <br />
              <hr />
              <br />
              {paragraphs}
          </article>
        )
      }
    }

    const mapStateToProps = state => {
      return {
        isBg: state.buttons.isBg
      };
    };
    
    const mapDispachToProps = dispatch => {
      return {
        onChangeLang: () => dispatch(actionCreator.changeLang())
      };
    };

    return connect(
      mapStateToProps,
      mapDispachToProps
    )(ArticleClass);
}

export default BlogArticle;