
import { CHANGE_LANG, ALERT, MENU_CLICKED, BEER_CLICKED } from './types';

export const changeLang = () => {
  return { type: CHANGE_LANG };
};

export const menuClicked = () => {
  return { type: MENU_CLICKED };
};

export const beerClicked = () => {
  return { type: BEER_CLICKED };
};

export const log = msg => {
  return { type: ALERT, payload: msg };
};
